/* You can add global styles to this file, and also import other style files */
@import "assets/styles/_general-vars.scss";

body {
  font: 400 15px -apple-system, BlinkMacSystemFont, sans-serif;
  line-height: 1.8;
  color: #818181;
}

h2 {
  font-size: 24px;
  text-transform: uppercase;
  color: #303030;
  font-weight: 600;
  margin-bottom: 30px;
}

h4 {
  font-size: 19px;
  line-height: 1.375em;
  color: #303030;
  font-weight: 400;
  margin-bottom: 30px;
}

.container-fluid {
  padding: 60px 50px;
}

.bg-grey {
  background-color: #f6f6f6;
}

.logo-small {
  color: $main-color;
  font-size: 50px;
}

.logo {
  color: $main-color;
  font-size: 200px;
}

